.skillsBlock {
    min-height: 100vh;
    background-color: #d4ffc0;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
    padding: 50px;
}

.title {
    background-color: #eeeef5;
}

.skills {
    border: 1px solid;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

